import Hero from '@/components/Hero';
import Slider from '@/components/Slider';
import ContactForm from "@/components/contactForm";
import SeccionTextoImagen from "@/components/Sections/SeccionTextoImagen"; 

async function getHomeData() {
  // Explicación de la URL:
  // 1. Buscamos la página 'home'
  // 2. Pedimos que 'popule' las secciones
  // 3. Agregamos [populate]=* para que entre a los componentes repetibles como 'Slides' y traiga la 'imagen'
  const url = `${process.env.NEXT_PUBLIC_STRAPI_URL}/api/home?populate[secciones][populate]=*`;
  
  const res = await fetch(url, { cache: 'no-store' });
  const json = await res.json();
  return json.data;
}

export default async function Home() {
  const data = await getHomeData();
  const secciones = data?.secciones || [];

  // Separamos los datos para cada componente
  const heroData = secciones.find((s: any) => s.__component === "sections.hero");
  const sliderData = secciones.find((s: any) => s.__component === "sections.slider-personalizable");

  return (
 <main>
  {secciones.map((seccion: any, index: number) => {
    // El 'case' debe coincidir exactamente con el nombre en Strapi
    switch (seccion.__component) {
      case "sections.hero":
        return <Hero key={index} {...seccion} />;

      case "sections.slider-personalizable":
        return <Slider key={index} Slides={seccion.Slides} />;

      case "sections.seccion-formulario": 
        return <SeccionTextoImagen key={index} data={seccion} />;

      default:
        return <p key={index}>Componente desconocido: {seccion.__component}</p>;
    }
  })}
</main>
  );
}